.main-cards-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 400px) {
  .cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .card-buttons {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
    width: 100%;
  }

  .card-button {
    width: 100px;
  }
}